import React, { Component } from 'react';
import { Transcript } from 'transcript-model';

import TranscriptEditor, { convertFromTranscript, convertToTranscript } from '../../src';
import '../../src/css/TranscriptEditor.css';

import VideoPlayer from './VideoPlayer';

const transcriptJson = require('../assets/media-tagger.json');
const video = require('../assets/video.mp4');

class EditorView extends Component {
  constructor(props) {
    super(props);

    const transcript = Transcript.fromMediaTagger(transcriptJson);
    const { editorState, speakers } = convertFromTranscript(transcript);

    this.state = {
      clipName: null,
      initialTranscript: null,
      playing: false,
      currentTime: 0,
      editorState,
      speakers,
    };
    this.handleChange = this.handleChange.bind(this);
    this.loadTranscript = this.loadTranscript.bind(this);
    this.handleLoadTranscript = this.handleLoadTranscript.bind(this);
    this.saveTranscript = this.saveTranscript.bind(this);
  }

  componentDidMount() {
    this.editor.focus();
  }

  handleChange({ editorState, speakers }) {
    this.setState({
      editorState,
      speakers,
    });
  }

  loadTranscript() {
    this.fileInput.click();
  }

  handleLoadTranscript() {
    const file = this.fileInput.files[0];
    this.fileInput.value = '';

    const fileReader = new FileReader();

    fileReader.onload = (event) => {
      const transcriptJSONString = event.target.result;
      const transcriptJSON = JSON.parse(transcriptJSONString);
      const transcript = Transcript.fromJSON(transcriptJSON);
      const { editorState, speakers } = convertFromTranscript(transcript);

      this.setState({
        editorState,
        speakers,
      });
    };

    fileReader.readAsText(file);
  }

  handleKeyboardEvent(keyboardEvent) {
    if (keyboardEvent.ctrlKey) {
      if (keyboardEvent.key === ' ') {
        console.log('play/pause');
        keyboardEvent.preventDefault();
        return;
      }
      if (keyboardEvent.key === 'f') {
        console.log('forwards');
        keyboardEvent.preventDefault();
        return;
      }
      if (keyboardEvent.key === 'b') {
        console.log('backwards');
        keyboardEvent.preventDefault();
      }
    }
  }

  saveTranscript() {
    const transcript = convertToTranscript(
      this.state.editorState.getCurrentContent(),
      this.state.speakers,
    );

    const blob = new Blob([JSON.stringify(transcript.toJSON(), null, 2)], {
      type: 'application/json;charset=utf-8',
    });

    window.open(URL.createObjectURL(blob));
  }

  render() {
    return (
      <div>
        <div className="row">
          <div className="col">
            <h2 style={{ marginTop: 0 }}>{this.state.clipName}</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-5">
            <div>
              <VideoPlayer
                src={video}
                onTimeUpdate={this.onTimeUpdate}
              />
              <div>
                <input
                  type="file"
                  ref={(c) => {
                    this.fileInput = c;
                  }}
                  style={{ display: 'none' }}
                  onChange={this.handleLoadTranscript}
                />
                <button onClick={this.loadTranscript}>Load transcript</button>
                <button onClick={this.saveTranscript}>Save transcript</button>
                <button
                  onClick={() => {
                    this.editor.focus();
                  }}
                >
                  Focus editor
                </button>
              </div>
            </div>
          </div>
          <div className="col-7">
            <TranscriptEditor
              ref={(editor) => {
                this.editor = editor;
              }}
              editorState={this.state.editorState}
              speakers={this.state.speakers}
              onChange={this.handleChange}
              onKeyboardEvent={this.handleKeyboardEvent}
              showSpeakers
            />
          </div>
        </div>
      </div>
    );
  }
}

export default EditorView;
